import Vue from 'vue';
import Vuex from 'vuex'; // 引入状态管理

Vue.use(Vuex);

// 注册状态管理全局参数
const vuex = new Vuex.Store({
  state: {
    // user_id: '',
    token: '',
  },
  mutations: {
    addToken(state, token) {
      localStorage.setItem('token', token);
      state.token = token;
    },
    removeToken(state) {
      localStorage.removeItem('token');
      state.token = undefined;
    },
  },
});
export default vuex;



